import {AWidget} from "./abstract/a-widget";
import jquery = require("jquery");

/**
 * Classe que exibe uma imagem
 * 
 * @extends {AWidget}
 * @memberof module:mvcomponents/widget
 */
export class Img extends AWidget{
	constructor(){
		super("div",`<a href="#" class="thumbnail">
				<img class="Img">
				</a>`);     
		jquery(this.element)
		.addClass("col-xs-12 col-md-12")
		.css({"padding-top":"5px"});	
	}
	public setSource(p_source:string):void{
		jquery(this.element).find(".Img").attr("src",p_source);
	}   
}
